body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.dashboard-page{
    padding: 40px 40px 40px 40px;
    background: linear-gradient(180deg, #FC8A8A 0%, #FFD3B3 50%);
    background-size: 100vw 189px;
    background-repeat: no-repeat;;
}
.chart-holder{
    height: calc(100vw - 84px);
    margin-top: 15px;
    width: calc(100vw - 84px);
    border: 2px solid #FFDCDC;
}
.add-modal{
    position: fixed;
    top: calc( 50vh - 160px);
    height: 300px;
    width: calc( 100vw - 84px);
    left: 38px;
    background-color: white;
    border: 2px solid #FFDCDC;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 27px;
}
.bottom-nav{
    width: 100%;
    position: fixed;
    bottom: 0;
}
.center-fab{
    height: 100px;
    width: 100px;
    box-sizing: border-box;
    padding-top: 17px;
    text-align: center;
    border: 5px solid #FFAFA4;
    border-radius: 50%;
    color: #FFAFA4;
    background-color: white;
}
.page{
    height: calc(100vh - 56px);
    box-sizing: border-box;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
}
